<template>
  <div class="flex1 pl30 pr30">
    <el-tabs v-model="activeName" class="authority-tab">
      <el-tab-pane label="个人信息" name="first">
        <el-col :span="24">
          <div class="section-title mt30">
            <span class="title">基本信息</span>
          </div>
          <el-form ref="form" :model="service" :rules="rules" label-width="110px" class="w800">
            <div class="pl10 pt20" style="max-width: 800px">
              <el-col :span="16">
                <el-form-item label="姓名:" prop="name">
                  <el-input disabled v-model="baseInfo.name" placeholder="请输入企业名称"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="16">
                <el-form-item label="账号:" prop="id">
                  <el-input disabled v-model="baseInfo.id" placeholder="18位字符"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="16">
                <el-col :span="15">
                  <el-form-item label="手机号:" prop="phone">
                    <el-input disabled v-model="baseInfo.phone" placeholder="请输入企业联系人的真实姓名"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="4" class="ml20">
                  <el-button type="text" v-if="baseInfo.phone" @click='handleBandClick'>重新绑定手机号</el-button>
                </el-col>
              </el-col>
              <el-col :span="16">
                <el-form-item label="部门:" prop="department">
                  <el-input v-model="baseInfo.department" placeholder="请输入部门"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="16">
                <el-form-item label="出生日期:" prop="bornDate">
                  <el-input v-model="baseInfo.bornDate" placeholder="请输入企业联系人的真实姓名"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="16">
                <el-form-item label="入职日期:" prop="bornDate">
                  <el-input v-model="baseInfo.entryDate" placeholder="请输入企业联系人的真实姓名"></el-input>
                </el-form-item>
              </el-col>
            </div>
          </el-form>
        </el-col>
        <el-col :span="24">
          <div class="section-title mt30">
            <span class="title">其他信息</span>
          </div>
          <el-form ref="first" :model="service" :rules="rules" label-width="110px" class="w800">
            <div class="pl10 pt20" style="max-width: 800px">
              <el-col :span="16">
                <el-form-item label="邮箱:" prop="email">
                  <el-input v-model="extraInfo.email" placeholder="请输入企业名称"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="16">
                <el-form-item label="电话:" prop="call">
                  <el-input v-model="extraInfo.call" placeholder="18位字符"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="16">
                <el-form-item label="微信号:" prop="weChat">
                  <el-input v-model="extraInfo.weChat" placeholder="请输入企业联系人的真实姓名"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="16">
                <el-form-item label="备注:" prop="note">
                  <el-input v-model="extraInfo.note" placeholder="请输入部门"></el-input>
                </el-form-item>
              </el-col>
            </div>
          </el-form>
        </el-col>
        <div class="w550 tc">
          <el-button type="primary" @click="doApply">保存</el-button>
        </div>
      </el-tab-pane>
      <el-tab-pane label="提醒设置" name="second">
        <div class="w550 fs14">
          <h3>未选中的消息类型将停止推送</h3>
          <el-form ref="second" :model="remind" :rules="rules" label-width="110px" class="w800">
            <el-form-item label="单据审核">
              <el-checkbox-group v-model="remind.oaReview">
                <el-checkbox label="单据指派我审核"></el-checkbox>
                <el-checkbox label="我发起的单据发生变更时"></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
            <el-form-item label="设备资产管理">
              <el-checkbox-group v-model="remind.assetManage">
                <el-checkbox label="备品备件库存下限预警"></el-checkbox>
                <el-checkbox label="设备盘盈"></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
            <el-form-item label="运维管理">
              <el-checkbox-group v-model="remind.operationsManage">
                <el-checkbox label="工单分派给我及状态变更"></el-checkbox>
                <el-checkbox label="工单分派到我所在的部门员工"></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-form>
          <div class="w550 tc">
            <el-button type="primary" @click="doApply">保存</el-button>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="修改密码" name="third">
        <div class="w350 fs14">
          <h3>修改密码：</h3>
          <el-col :span="22">
            <el-form :model="password" :rules="rules" label-width="110px" class="w800">
              <el-col :span="24">
                <el-form-item label="输入旧密码" prop="old">
                  <el-input
                    v-model='password.old'>
                  </el-input>
                </el-form-item>
              </el-col>
              <el-col :span="24">
                <el-form-item label="输入新密码" prop="new">
                  <el-input
                    v-model='password.new'>
                  </el-input>
                </el-form-item>
              </el-col>
            </el-form>
          </el-col>
          <div class="w350 tc">
            <el-button type="primary" @click="doApply">保存</el-button>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
    <ChangePassword @closeDialog="closeAddDialog" :dialogFormVisible="changePwdShow"></ChangePassword>
  </div>
</template>

<script>
  import ChangePassword from './ChangePassword.vue'
  export default {
    components: {ChangePassword},
    data() {
      return {
        changePwdShow: false,
        activeName: 'first',
        baseInfo: {
          bornDate: '',
          department: '',
          phone: '123',
          name: '',
          id: '',
          entryDate: ''
        },
        extraInfo: {
          note: '',
          email: '',
          call: '',
          weChat: ''
        },
        remind: {
          oaReview: [],
          assetManage: [],
          operationsManage: []
        },
        password: {
          old: '',
          new: ''
        }
      }
    },
    methods: {
      handleBandClick() {
        this.changePwdShow = true
      },
      closeAddDialog() {
        this.changePwdShow = false
      }
    }
  }
</script>


