<template>
  <div>
    <el-dialog :visible.sync="visible" :before-close="beforeClose" :append-to-body="true" title="重新绑定手机号" width="500px">
      <div class="p15">
        <el-form :show-message=true :inline-message=true ref="form" :model="form" :rules="rules" class="form-text f13 " label-width="108px" size="small">
          <el-row>
            <el-col :span="20">
              <el-form-item label="输入密码：" prop="name">
                <el-input
                  placeholder='请输出单位名称'
                  v-model='form.name'>
                </el-input>
              </el-form-item>
            </el-col>
            <el-col :span="20" class="mt15">
              <el-form-item label="新手机：" prop="name">
                <el-input
                  placeholder='请输出单位名称'
                  v-model='form.name'>
                </el-input>
              </el-form-item>
            </el-col>
            <el-col :span="20" class="mt15">
              <el-col :span="15">
                <el-form-item label="验证码:" prop="phone">
                  <el-input v-model="form.name"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="4" class="ml15 prefix_button">
                <el-button type="text" @click='handleBandClick'>发送验证码</el-button>
              </el-col>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer mt10" style="text-align: center">
        <el-button type="primary" @click="closeDialog('save')">保 存</el-button>
        <el-button @click="closeDialog('close')">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    name: 'Dialog',
    props: ['dialogFormVisible'],
    computed: {
      visible: {
        get: function () {
          return this.dialogFormVisible
        },
        set: function () {
        }
      }
    },
    data() {
      return {
        showModal: false,
        form: {
          name: ''
        },
        rules: {
          name: [
            { required: true }
          ]
        }
      }
    },
    methods: {
      closeDialog(method) {
        this.$emit('closeDialog', this.form, method)
        if (method !== 'close') {
          this.form = {
            name: ''
          }
          this.$refs['form'].resetFields()
        }
      },
      modalShow() {
        this.showModal = true
      },
      closeModal(value) {
        this.form.department = value
      },
      beforeClose(done) {
        done()
        this.$emit('closeDialog', {}, 'close')
      }
    }
  }
</script>
<style lang="scss">
  .dialog_input_custom{
    >input{
      height: 28px;
      font-size: 12px;
      top: 1px;
      position: relative;
    }
  }
  .dialog_date_custom{
    .el-input__prefix{
      top: -6px;
    }
  }
  .prefix_button{
    position: relative;
    top: -4px;
  }
</style>


